
import { ON_CART_FINISHED, addToCart, t } from "@process-creative/slate-theme-tools";
import KeenSlider, { TOptionsEvents } from "keen-slider";
import { onAddClicked, onCartUpdated, updateButton, Bundle, addBundleToCart, validateBundleAvailability } from "../objects/bundle/bundle-common";
import { initCarousel, setSliderBasedOnViewWidth } from "../objects/carousel";
import { getElem, getElems } from "../tools/utilities/helper-functions";

const mainContainer = getElem('[data-bundle-slider]')!;

const trackBundleChildInventory = !!mainContainer.dataset.trackChildInventory;

declare const window: Window & {
  bundleSlider: {
    bundles: Bundle[]
  }
}

if(mainContainer){
  setupSlider();
  setupAddToCart();
}


function setupSlider(){
  

  const slidesContainer = getElem('[data-slides-container]', mainContainer)!;
  const slideElems = getElems('[data-slider-slide]', slidesContainer)
 

  setSliderBasedOnViewWidth(setupCarousel, slidesContainer, slideElems)  

  function setupCarousel(initialSlidesPerView: number) {
    return initCarousel({
      container: slidesContainer,
      options: {
        centered: true,
        loop: true,
        initial: 1,
        mode: 'snap',
        duration: 600,
        slidesPerView: initialSlidesPerView
      },
      dotsContainer: getElem('[data-carousel-dots]')
    })!.slider;    
  }  
}



function setupAddToCart(){
  
  const sliderBundles: Bundle[] = window.bundleSlider.bundles.map(bundle => ({
    ...bundle,
    percentageDiscount: Number(bundle.percentageDiscount)
  }))
  if(!!sliderBundles?.length == false){
    console.error("Bundle Slider should have window obj data")
  }

  sliderBundles.forEach(bundle => {
    const {bundleProduct} = bundle
    const bundleContainer = getElem(`[data-bundle-product-id="${bundleProduct.id}"]`, mainContainer)!;


    const addToCartButton = getElem('[data-add-bundle-button]', bundleContainer)!;
    const baseClass = 'c-bundle-slider';
    updateButton(
      addToCartButton, 
      validateBundleAvailability(bundle),
      bundle,
      baseClass
    );

    onAddClicked(addToCartButton, async () => {
      if (trackBundleChildInventory)
        addBundleToCart({bundle, addToCartButton});
      else {
        try {
          await addToCart(bundle.bundleProduct.variants[0].id, 1);
        } catch(e: any){
          alert(e.description);
          addToCartButton.innerHTML = t('products.product.error');
        }
      }
    });

    onCartUpdated(() => {
      updateButton(
        addToCartButton, 
        validateBundleAvailability(bundle),
        bundle,
        baseClass
      );
    });

  });


}
